import React, { useState, useEffect } from "react";
import axios from "axios";
import "./VistaNegocio.css";

const VistaNegocio = () => {
  const [negocios, setNegocios] = useState([]);
  const [propietarios, setPropietarios] = useState({}); // Almacenar los propietarios por ID de usuario
  const [loading, setLoading] = useState(true); // Estado para el mensaje de carga
  const [editingId, setEditingId] = useState(null); // ID del negocio que se está editando
  const [editForm, setEditForm] = useState({}); // Datos del formulario de edición

  // Función para obtener los negocios desde la API
  const fetchNegocios = async () => {
    setLoading(true); // Mostrar mensaje de carga mientras se obtienen datos
    try {
      let negociosTemp = []; // Para almacenar los negocios obtenidos

      // Intentar obtener negocios para diferentes userId comenzando desde 1
      let userId = 1;
      let moreDataAvailable = true;

      while (moreDataAvailable) {
        const response = await axios.get("https://api-degusto.onrender.com/api/business", {
          params: {
            userId: userId, // Enviar el userId incrementado
            businessName: "",
            businessStatus: "", // Mostrar todos los estados
          },
        });

        if (response.data.length > 0) {
          negociosTemp = [...negociosTemp, ...response.data]; // Añadir negocios a la lista
        } else {
          moreDataAvailable = false; // Si no hay más negocios, detener el ciclo
        }

        userId++; // Incrementar el userId para la siguiente iteración
      }

      setNegocios(negociosTemp);

      // Obtener propietarios
      const propietariosPromises = negociosTemp.map((negocio) =>
        axios.get(`https://api-degusto.onrender.com/api/user/${negocio.business_UserId}`)
      );

      const propietariosResponses = await Promise.all(propietariosPromises);
      const propietariosData = propietariosResponses.reduce((acc, res) => {
        acc[res.data.userId] = res.data.personName; // Guardar el nombre del propietario
        return acc;
      }, {});

      setPropietarios(propietariosData);
    } catch (error) {
      console.error("Error al obtener negocios o propietarios:", error);
    } finally {
      setLoading(false); // Ocultar mensaje de carga
    }
  };

  useEffect(() => {
    fetchNegocios();
  }, []);

  // Función para manejar la validación de un negocio
  const handleValidate = async (id) => {
    try {
      await axios.put("https://api-degusto.onrender.com/api/business/states", {
        businessId: id,
        businessStatus: 2, // Cambiar el estado a Validado
      });
      fetchNegocios(); // Actualizar la lista
    } catch (error) {
      console.error("Error al validar el negocio:", error);
    }
  };

  // Función para manejar la denegación de un negocio
  const handleDeny = async (id) => {
    try {
      await axios.put("https://api-degusto.onrender.com/api/business/states", {
        businessId: id,
        businessStatus: 1, // Cambiar el estado a Pendiente
      });
      fetchNegocios(); // Actualizar la lista
    } catch (error) {
      console.error("Error al denegar el negocio:", error);
    }
  };

  // Función para manejar la edición de un negocio
  const handleEdit = (negocio) => {
    setEditingId(negocio.businessId);
    setEditForm({
      businessName: negocio.businessName,
      businessAddress: negocio.businessAddress,
      businessPhoneNumber: negocio.businessPhoneNumber,
      businessStatus: negocio.businessStatus,
    });
  };

  const saveEdit = async (id) => {
    try {
      await axios.put(`https://api-degusto.onrender.com/api/business/${id}`, {
        businessName: editForm.businessName,
        businessAddress: editForm.businessAddress,
        businessPhoneNumber: editForm.businessPhoneNumber,
        businessStatus: editForm.businessStatus,
        businessLatitude: 1,
        businessLongitude: -1,
        businessCategorization: 1,
        business_AreaId: 1,
        business_UserId: 1, // Cambiado a una constante general
        file: null,
      });
      setEditingId(null);
      fetchNegocios(); // Actualizar la lista
    } catch (error) {
      console.error("Error al guardar los cambios:", error);
    }
  };

  // Función para manejar la eliminación de un negocio
  const handleDelete = async (id) => {
    try {
      await axios.delete(`https://api-degusto.onrender.com/api/business/${id}`);
      fetchNegocios(); // Actualizar la lista
    } catch (error) {
      console.error("Error al eliminar el negocio:", error);
    }
  };

  return (
    <div className="negocio-table-container">
      <h2>Lista de Negocios</h2>
      {loading ? (
        <p>Cargando...</p>
      ) : (
        <table className="negocio-table">
          <thead>
            <tr>
              <th>#</th>
              <th>Nombre Negocio</th>
              <th>Dirección</th>
              <th>Número</th>
              <th>Propietario</th>
              <th>Estado</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {negocios.map((negocio, index) => (
              <tr key={negocio.businessId}>
                {/* Número incremental basado en el índice */}
                <td>{index + 1}</td>
                <td>
                  {editingId === negocio.businessId ? (
                    <input
                      type="text"
                      value={editForm.businessName}
                      onChange={(e) =>
                        setEditForm({ ...editForm, businessName: e.target.value })
                      }
                    />
                  ) : (
                    negocio.businessName
                  )}
                </td>
                <td>
                  {editingId === negocio.businessId ? (
                    <input
                      type="text"
                      value={editForm.businessAddress}
                      onChange={(e) =>
                        setEditForm({ ...editForm, businessAddress: e.target.value })
                      }
                    />
                  ) : (
                    negocio.businessAddress
                  )}
                </td>
                <td>
                  {editingId === negocio.businessId ? (
                    <input
                      type="text"
                      value={editForm.businessPhoneNumber}
                      onChange={(e) =>
                        setEditForm({ ...editForm, businessPhoneNumber: e.target.value })
                      }
                    />
                  ) : (
                    negocio.businessPhoneNumber
                  )}
                </td>
                <td>{propietarios[negocio.business_UserId] || "Cargando..."}</td>
                <td>
                  {negocio.businessStatus === 1
                    ? "Pendiente"
                    : negocio.businessStatus === 2
                    ? "Validado"
                    : ""}
                </td>
                <td>
                  {editingId === negocio.businessId ? (
                    <button onClick={() => saveEdit(negocio.businessId)}>
                      Guardar
                    </button>
                  ) : (
                    <>
                      <button
                        className="btn-validar"
                        onClick={() => handleValidate(negocio.businessId)}
                      >
                        Validar
                      </button>
                      <button
                        className="btn-denegar"
                        onClick={() => handleDeny(negocio.businessId)}
                      >
                        Denegar
                      </button>
                      <button
                        className="btn-editar"
                        onClick={() => handleEdit(negocio)}
                      >
                        Editar
                      </button>
                      <button
                        className="btn-eliminar"
                        onClick={() => handleDelete(negocio.businessId)}
                      >
                        Eliminar
                      </button>
                    </>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default VistaNegocio;